<template>
    <div class="tables-box">
        <breadcrumb :breadcrumbs='breadcrumb'></breadcrumb>
        <!--  -->
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            label=""
            type="index"
            width="60">
            </el-table-column>
            <el-table-column
            prop="users"
            label=""
            >
            </el-table-column>
            <el-table-column
            fixed="right"
            label=""
            >
            <template slot-scope="scope">
                <el-button @click="handleClickB(scope.row)" type="text" size="small"></el-button>
            </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
import Breadcrumb from './breadcrumb/Breadcrumb'
export default {
    data() {
      return {
        breadcrumb:['','',''],
        tableData: [],  //
      }
    },
    created:function(){
        // 
        this.reverseUser()
       
    },
    methods:{
        handleClickB:function(row){  //
        if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }).then(() => {
                    
                    this.$http({
                        url:'removeNames',
                        method:"POST",
                        data:{
                            userid:row._id
                        }
                    }).then(res=>{
                        if(res.data.code==0){
                            this.$message({
                                type: 'success',
                                message: ''
                            });  
                            for(let i=0;i<this.tableData.length;i++){
                                    if(this.tableData[i]['_id']==row._id){
                                        this.tableData.splice(i,1) 
                                    }
                                }
                        }

                    }).catch(()=>{
                         this.$message({
                                type: 'warning',
                                message: ''
                            });  
                    })
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        },
        reverseUser:function(){
            this.$http({
                url:'allNames',
                method:'POST',
            })
            .then(res=>{
              //  console.log(res)
                this.tableData=res.data.data
            })
        }
    },
    components:{
        Breadcrumb
    }
}
</script>

<style scoped>
.foots{
    margin-top: 30px;
}
.tables-box{
    height: 100%;
    overflow-y: scroll;
}
</style>
