<template>
    <div class="loads">
        <div class="loads-box">
            <!--  -->
            <el-form ref="TabForm" :model="formTab" label-width="0" :rules="formTabs">
                <el-form-item  prop="users">
                    <el-input v-model="formTab.users" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="name">
                    <el-input v-model="formTab.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="passwords">
                    <el-input  v-model="formTab.passwords" placeholder=""  show-word-limit></el-input>
                </el-form-item>
                <el-form-item prop="desc">
                    <el-select v-model="formTab.desc" placeholder="">
                        <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item prop="numbers">
                    <el-input v-model="formTab.numbers" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit('TabForm')"></el-button>
                </el-form-item>
            </el-form>    
        </div>
    </div>
</template>

<script>
import axios from '../axios.js'
export default {
    data(){
        let validateName=function(rule, value, callback){  //
              axios({
                  url:'searchNames',
                  method:"POST",
                  data:{
                      name:value
                  }
                  }).then(res=>{
                      if(res.data.code==0){
                            if(res.data.data>0){
                                callback(new Error(''));
                            }else{
                                callback();
                                
                            }
                        }else{
                            callback(new Error('2'));
                        }
                  }).catch(error=>{
                       callback(new Error('1'));
                  })  
                
        };
        let validatereg=function(rule, value, callback){   //
                    let reg=/^[a-zA-Z][a-zA-Z0-9]{3,15}$/;  
                      if(reg.test(value)==true){
                            
                                callback();
                            
                        }else{
                            callback(new Error('(,)'));
                        }
                 
                
        };
         let validatePassReg=function(rule, value, callback){   //
                    let reg=/^[a-zA-Z][a-zA-Z0-9]{6,30}$/;  
                      if(reg.test(value)==true){
                            
                                callback();
                            
                        }else{
                            callback(new Error('()'));
                        }
                 
                
        };
        let validateUserReg=function(rule, value, callback){   //
                    let reg=/[\u4e00-\u9fa5_a-zA-Z0-9_]{2,15}/;  
                      if(reg.test(value)==true){
                                callback();     
                        }else{
                            callback(new Error('()'));
                        }     
        };
        return {
            formTab:{
                users:'',
                name:'',
                passwords:'',
                desc:'',
                numbers:''
            },
            formTabTsxt:{
                name:'test',
                passwords:'test1234',
                checked:false
            },
            formTabs:{  //
                users:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' },
                    { validator: validateUserReg, trigger: 'blur' }
                ],
                name:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' },
                    { validator: validatereg, trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                passwords:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 6, max: 30, message: ' 6  30 ', trigger: 'blur' },
                    { validator: validatePassReg, trigger: 'blur' }
                ],
                desc:[
                    { required: true, message: '', trigger: 'blur' }
                ],
                numbers:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' },
                ]
            },
            options: [{
                value: '1',
                label: ''
                }, {
                value: '2',
                label: ''
                }] 
        }
               
    },
    methods:{
        onSubmit:function(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$http({
                        method:'POST',
                        url: 'adduser',
                        data:this.formTab
                    }).then((res)=>{
                       
                        if(res.data.code==0){
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'success'
                            });
                            this.$router.push('/')
                        }else if(res.data.code==2){
                            this.$message.error('');
                        }else{
                            this.$message.error('');
                        }
                        this.formTab.users=''
                        this.formTab.name=''
                        this.formTab.passwords=''
                        this.formTab.numbers=''
                    }).catch(()=>{

                    })
                }else{

                }

            })
        }
        
    },
    components:{

    },
    created:function(){ 
            
            
  }
}
</script>

<style scoped>
.loads{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loads-box{
    width: 420px;
    height: 400px;
    padding: 20px 20px;
}
.loads-box .el-button--primary{
    width: 100%;
}
.texts{
    text-align: center;
}
.passwords{
    text-align: right;
}
</style>
