<template>
    <div class="box">
        <breadcrumb :breadcrumbs='breadcrumb' class="box-title"></breadcrumb>
        <!--  -->
        <el-row class="conts">
            <el-col class="write-btns" :xs="{span: 8, offset: 8}" :sm="{span: 6, offset: 12}" :md="{span: 4, offset: 16}" :lg="{span: 3, offset: 18}" :xl="{span: 2, offset: 20}" justify="end">
                <div class="grid-content bg-purple-dark">
                    <el-button type="info" @click="submitForm('ruleForm','2')"></el-button>
                </div>
            </el-col>
            <el-col class="write-btns" :xs="{span: 8}" :sm="{span: 6}" :md="{span: 4}" :lg="{span: 3}" :xl="{span: 2}" justify="end">
                <div class="grid-content bg-purple-dark">
                    <el-button type="success" @click="submitForm('ruleForm','1')"></el-button>
                </div>
            </el-col>
        </el-row>
        <el-form ref="ruleForm" :model="form" label-width="100px" :rules="rulesform">
            <el-form-item label=":" prop="name">
                <el-input v-model="form.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label=":" prop="introduction">
                <el-input type="textarea" v-model="form.introduction" placeholder=""  maxlength="150" show-word-limit></el-input>
            </el-form-item>
            <el-form-item label=":" prop="classed">
                <el-select v-model="form.classed" placeholder="">
                    <el-option v-for="(item,index) in clsList" :key="index"  :label="item.name" :value="item._id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" prop="checkedA">
                    
                    <el-select
                        v-model="form.checkedA"
                        multiple
                        :size="modes"
                        allow-create
                        class="selectCls"
                        :multiple-limit="multipleLimit"
                        default-first-option
                        placeholder="">
                        <el-option
                        v-for="item in tablesList"
                        :key="item.value"
                        :label="item.name"
                        :value="item._id">
                        </el-option>
                    </el-select>
       
            </el-form-item>
            <!-- <el-button @click="mackdownSubmit"></el-button> -->
            <el-form-item label=":" prop="tx">
                <div class="write-texts">
                    <!-- <mark-down @on-save='mackdownSubmit' value='' /> -->
                    <mavon-editor ref=md @imgAdd="$imgAdd" :toolbars='toolbars'  v-model="form.tx" @save='saveStr'/>
                </div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import Breadcrumb from '../breadcrumb/Breadcrumb'
// import MarkDown from 'vue-meditor'
export default {
    data(){
        return {
            breadcrumb:['(MarkDown)'],
            autoSaves:true,
            form:{  //
                name:'',  //
                introduction:'',  //
                classed:'',  //
                checkedA:[],  //
                tx:'',   //
                bst: 2 //12markDown
               
            },
            rulesform:{ //
                name:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
                ],
                introduction:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 10, max: 150, message: ' 10  150 ', trigger: 'blur' }
                ],
                classed:[
                    { required: true, message: '', trigger: 'change' }
                ],
                checkedA:[
                    { type: 'array', required: true, message: '', trigger: 'change' }
                ],
                tx:[
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            // clsList:[], //
            // tablesList:[], //
            modes:'mini',
            multipleLimit:5,  //
            value:'',
            toolbars: {
                bold: true, // 
                italic: true, // 
                header: true, // 
                underline: true, // 
                strikethrough: true, // 
                mark: true, // 
                superscript: true, // 
                subscript: true, // 
                quote: true, // 
                ol: true, // 
                ul: true, // 
                link: true, // 
                imagelink: true, // 
                code: true, // code
                table: true, // 
                fullscreen: true, // 
                readmodel: true, // 
                htmlcode: true, // html
                help: true, // 
                /* 1.3.5 */
                undo: true, // 
                redo: true, // 
                trash: true, // 
                save: true, // eventssave
                /* 1.4.2 */
                navigation: true, // 
                /* 2.1.8 */
                alignleft: true, // 
                aligncenter: true, // 
                alignright: true, // 
                /* 2.2.1 */
                subfield: true, // 
                preview: true, // 
            }
        }
    },
    components:{
        Breadcrumb
    },
    methods:{
        submitForm(formName,tp) {  //
          if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
        this.$refs[formName].validate((valid) => {
          if (valid) {
              
            // 
            this.$http({  
                method:'POST',
                url: 'updatewrite',
                data:{
                    forms:this.form,
                    types:tp
                }
            }).then((res)=>{
                //console.log(res.data)
                if(res.data.code==0){
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                        });
                    this.form.name=''
                    this.form.introduction=''
                    this.form.classed=''
                    this.form.checkedA=[]
                    this.form.tx=''
                    this.$router.go(-1)
                }else{
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                }
            })
          } else {
            return false;
          }
        });
      },
    saveShareContent:function (content, fileName) {
            let downLink = document.createElement('a')
            downLink.download = fileName
            //blod
            let blob = new Blob([content])
            downLink.href = URL.createObjectURL(blob)
            // 
            document.body.appendChild(downLink)
            downLink.click()
            // 
            document.body.removeChild(downLink)
        },
    saveStr:function(value){
            if(!value){
                return;
            }
            this.saveShareContent(value,'nodebook.md')
        },
        // @imgAdd event
        $imgAdd(pos, $file){
            // ..
           var formdata = new FormData();
           formdata.append('image', $file);
           this.$http({
               url: 'srcImgsMarkDown',
               method: 'POST',
               data: formdata,
               headers: { 'Content-Type': 'multipart/form-data' },
           }).then((url) => {
               // .url![...](0) -> ![...](url)
               // $vm.$img2Url 
               console.log(url.data.url)
               this.$refs.md.$img2Url(pos, url.data.url);
           })
        }
    },
    created:function(){
       // 
     // console.log(this.$route.params.userid)
        if(!this.$route.params.userid){
            this.$router.go(-1)
        }
        this.$http({
                method:'POST',
                url: 'searchtexsli',
                data:{
                    id:this.$route.params.userid
                }
            }).then((res)=>{
                console.log(res.data)
                if(res.data.code=='0'){
                    this.form=res.data.data[0]
                    this.$store.commit('addtxB',res.data.data[0]['tx'])
                    
                }
            })
    },
    computed:{
        clsList:function(){
            return this.$store.state.clsList
        },
        tablesList:function(){
            return this.$store.state.tablesList
        }
    },
    watch:{

    }
}
</script>

<style scoped>
.box{
    overflow-y:scroll;
}
.write-table{
    border: 1px solid #DCDFE6;
    border-radius: 5px;
    padding: 10px 10px;
}
.write-table-list{
    padding: 0px 20px;
    font-size: 12px;
    border: 1px solid #DCDFE6;
    border-radius: 20px;
    cursor: pointer;
    margin-right: 15px;
    display: inline-block;
    margin-bottom: 10px;
    background: #DCDFE6;
    line-height: 25px;
}
.write-texts{
    border: 1px solid #DCDFE6;
    border-radius: 5px;
    padding: 10px 10px;
}
.write-btns{
    margin-bottom: 5px;
}
.checkli{
    border-radius: 10px;
    margin-bottom: 10px;
}

.selectCls{
    width: 100%;
}


.box-title{
    overflow-y: hidden;
}
</style>
