<template>
    <div :class="[split.D==1 ? 'txts' : 'txtsB']">
        <!--  -->
        <el-row>
            <el-col :span="24">
                <div class="grid-content bg-purple-dark txt-title">
                   <el-col :xs="{span: 12, offset: 12}" :sm="{span: 8, offset: 16}" :md="{span: 6, offset: 18}" :lg="{span: 4, offset: 20}" :xl="{span: 3, offset: 21}">
                       <div class="grid-content bg-purple-light btns">
                           <i class="iconfont icon-quanping btns-icon" title="" @click="splitD"></i>
                           <i class="iconfont icon-xie btns-icon" title="" @click="splitA"></i>
                           <i class="iconfont icon-wenbentext72 btns-icon" title="" @click="splitB"></i>
                           <i class="iconfont icon-yulan btns-icon" title="" @click="splitC"></i>
                       </div>
                    </el-col>
                </div>
            </el-col>
            <el-col :span="split.A">
                <div class="grid-content bg-purplet">
                    <quill-editor
                        :class="[split.D==1 ? 'txt-left' : 'txt-leftB']" 
                        v-model="content" 
                        ref="myQuillEditor" 
                        :options="editorOption" 
                        @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
                        @change="onEditorChange($event)">
                    </quill-editor>
                </div>
            </el-col>
            <el-col :span="split.B" class="txt-ls">
                <div class="grid-content bg-purple-light txt-right">
                    <el-container>
                        <el-header class="add-heads" v-bind:style="{borderLeft: split.B===24 ? '1px solid #ccc' : ' 0px solid #ccc' }"></el-header>
                        <el-main  :class="[split.D==1 ? 'add-main' : 'add-mainB']"  v-bind:style="{borderLeft: split.B===24 ? '1px solid #ccc' : ' 0px solid #ccc' }">
                           <div class="ql-container ql-snow">
                                <div class="ql-editor">
                                    <div v-html="content"></div>
                                </div>
                            </div>
                        </el-main>
                    </el-container>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
 import '../assets/fontsize/fonttxt.css';
 import '../assets/css/vue-quill.css'
 import { quillEditor } from 'vue-quill-editor'
 import * as Quill from 'quill'  //

    import "quill/dist/quill.core.css";//
    import "quill/dist/quill.snow.css";
 //
    let fonts = ['SimSun'];  
    let Font = Quill.import('formats/font');  
    Font.whitelist = fonts; // 
    Quill.register(Font, true);

//
import { ImageDrop } from 'quill-image-drop-module';
import { request } from 'http';
    
Quill.register('modules/imageDrop', ImageDrop);

//quill
// const ImageResize = require('../../node_modules/_quill-image-resize-module@3.0.0@quill-image-resize-module/image-resize.min.js')
    // import  ImageResize  from 'quill-image-resize-module'
    
    // Quill.register('modules/imageResize', ImageResize)

// 
let toolbarList=[
    ['bold','italic','underline','strike','blockquote','code-block'],
    [{'header':1},{'header':2}],
    [{ 'list': 'ordered'}, { 'list': 'bullet' },{ 'indent': '-1'}, { 'indent': '+1' },{ 'color': [] }, { 'background': [] },{ 'align': [] }],
    ['image','clean'],
    [{ 'font': fonts }]
]

// 
/**/
const uploadConfig = {
    action:  'http://api.nodebook.top/bookadmin/src-imgs',  //  
    methods: 'POST',  //  
    // token: '',  //  tokentokensessionStorage
    name: 'imgstr',  //  
    size: 500,  //    Kb, 1M = 1024Kb
    accept: 'image/png, image/gif, image/jpeg, image/bmp, image/x-icon'  //  
};

const handlers = {
    image: function image() {
        var self = this;
 
        var fileInput = this.container.querySelector('input.ql-image[type=file]');
        if (fileInput === null) {
            fileInput = document.createElement('input');
            fileInput.setAttribute('type', 'file');
            // 
            if (uploadConfig.name) {
                fileInput.setAttribute('name', uploadConfig.name);
            }
            // 
            fileInput.setAttribute('accept', uploadConfig.accept);
            fileInput.classList.add('ql-image');
            // 
            fileInput.addEventListener('change', function () {
                // formData
                var formData = new FormData();
                formData.append(uploadConfig.name, fileInput.files[0]);
                formData.append('object','product');
                // tokentoken
                // if (uploadConfig.token) {
                //     formData.append('token', uploadConfig.token)
                // }
                // 
                var xhr = new XMLHttpRequest();
                xhr.open(uploadConfig.methods, uploadConfig.action, true);
                // 
                xhr.onload = function (e) {
                    if (xhr.status === 200) {
                        var res = JSON.parse(xhr.responseText);
                        let length = self.quill.getSelection(true).index;
                        //imgsrcres.path 
                       // console.log(res)           
                        self.quill.insertEmbed(length, 'image', res.url);
                        self.quill.setSelection(length + 1)
                    }
                    fileInput.value = ''
                };
                // 
                xhr.upload.onloadstart = function (e) {
                    fileInput.value = ''
                };
                // 
                xhr.upload.onerror = function (e) {
                };
                // 
                xhr.upload.onloadend = function (e) {
                    // console.log('')
                };
                xhr.send(formData)
            });
            this.container.appendChild(fileInput);
        }
        fileInput.click();
    }
};



export default {
    data(){
        return{
            content:null,
            editorOption:{
                placeholder:"",
                modules:{
                       
                        toolbar:{
                            container: toolbarList, //   
                            handlers: handlers  //  
                        }, //
                        imageDrop:true //
                        // imageResize: {} 
                    }
            },
            theme:'bubble',
            split:{
                A:12,
                B:12,
                D:1
            },
            imgUrl:process.env.VUE_APP_IMGURL
        }
    },
    methods:{
            onEditorBlur:function(ev){//
               // console.log(ev.html)
            },
            onEditorFocus:function(ev){//
               // console.log(ev.html)
            },
            onEditorChange:function(ev){//
               // this.$emit('addTxt',ev.html)
               this.$store.commit('addtx',ev.html)

            },
            splitA:function(){  //
                    this.split.A=24,
                    this.split.B=0 
            },
            splitB:function(){  //
                    this.split.A=12,
                    this.split.B=12
            },
            splitC:function(){  //
                    this.split.A=0,
                    this.split.B=24
            },
            splitD:function(){
                //console.log(this.split.D)
                    this.split.D= -this.split.D
            }
        
    },
    components:{
        quillEditor
    },
    computed:{
        tx:function(){
            return this.$store.state.tx
        }
    },
    watch:{
        tx:function(news,olds){
            if(news.length<1){
                this.content=news
            }
        }
    }
}

</script>

