<template>
    <div class="tables-box">
        <breadcrumb :breadcrumbs='breadcrumb'></breadcrumb>
        <!--  -->
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            label=""
            type="index"
            width="60">
            </el-table-column>
            <el-table-column
            prop="names"
            label=""
            >
            </el-table-column>
            <el-table-column
            prop="texts"
            label=""
            >
            </el-table-column>
            <el-table-column
            prop="strs"
            label=""
            >
            </el-table-column>
            <el-table-column
            fixed="right"
            label=""
            >
            <template slot-scope="scope">
                <el-button @click="handleClickB(scope.row)" type="text" size="small"></el-button>
                <el-button @click="handleClickAX(scope.row)" type="text" size="small"></el-button>
                <el-button @click="handleClickB(scope.row)" type="text" size="small"></el-button>
            </template>
            </el-table-column>
        </el-table>
        <el-row class="foots">
                <el-col :xs="{span: 24}" :sm="{span: 18}" :md="{span: 12, offset: 8}" :lg="{span: 8, offset: 12}" :xl="{span: 6, offset: 16}" justify="end">
                    <div class="grid-content bg-purple-dark">
                            <el-pagination
                                background
                                layout="prev, pager, next"
                                :total="AllNum"
                                :page-size= 'numA'
                                @current-change="handleCurrentChange"
                                :hide-on-single-page="AllNum/Number>1 ? false : true ">
                            </el-pagination>
                    </div>
                </el-col>
        </el-row>
    </div>
</template>

<script>
import Breadcrumb from './breadcrumb/Breadcrumb'
export default {
    data() {
      return {
        breadcrumb:['',''],
        tableData: [],  //
        Number: 10,  //
        num:1,  //
        numA:10,  //
        AllNum:0  //
      }
    },
    created:function(){
        // 
         this.reverseUser()
            this.$http({
                    url:'searchCommontsAllNumber',
                    method:'POST'
                })
                .then(res=>{
                  
                    this.AllNum=res.data.data
                }).catch(()=>{
                    console.log('')
                })
    },
    methods:{
        handleCurrentChange:function(val){
                this.num=val
                this.reverseUser()
        },
        handleClickB:function(row){  //
            if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }).then(() => {
                    
                    this.$http({
                        url:'removesCommonts',
                        method:"POST",
                        data:{
                            userid:row._id
                        }
                    }).then(res=>{
                        if(res.data.code==0){
                            this.$message({
                                type: 'success',
                                message: ''
                            });  
                            for(let i=0;i<this.tableData.length;i++){
                                    if(this.tableData[i]['_id']==row._id){
                                        this.tableData.splice(i,1) 
                                    }
                                }
                        }

                    }).catch(()=>{
                         this.$message({
                                type: 'warning',
                                message: ''
                            });  
                    })
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        },
         handleClickAX:function(row){  //
            if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }).then(() => {
                    
                    this.$http({
                        url:'updataCommonts',
                        method:"POST",
                        data:{
                            userid:row._id
                        }
                    }).then(res=>{
                        if(res.data.code==0){
                            this.$message({
                                type: 'success',
                                message: ''
                            });  
                            for(let i=0;i<this.tableData.length;i++){
                                    if(this.tableData[i]['_id']==row._id){
                                        this.tableData[i].strs='' 
                                    }
                                }
                        }

                    }).catch(()=>{
                         this.$message({
                                type: 'warning',
                                message: ''
                            });  
                    })
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        },
        reverseUser:function(){
            this.$http({
                url:'searchCommonts',
                method:'POST',
                data:{
                    num:this.num,
                    Number:this.Number
                }
            })
            .then(res=>{
                
                this.tableData=res.data.data
                if(res.data.data.length>0){
                    
                    for(let i=0;i<res.data.data.length;i++){
                       
                        this.tableData[i].strs=res.data.data[i].types== '1' ? '' : ''
                    }
                  
                }
            }).catch(()=>{
                console.log('')
            })
        }
    },
    components:{
        Breadcrumb
    }
}
</script>

<style scoped>
.foots{
    margin-top: 30px;
}
.tables-box{
    height: 100%;
    overflow-y: scroll;
}
.foots{
    margin-top: 30px;
}
</style>
