<template>
    <div class="txtbox">
        <breadcrumb :breadcrumbs='breadcrumb'></breadcrumb>
        <!--  -->
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column
                type="index"
                width="50"
                label="">
            </el-table-column>
            <el-table-column
            prop="name"
            label="">
            </el-table-column>
            <el-table-column
            prop="addressA"
            label="">
            </el-table-column>
            <el-table-column
            prop="addressB"
            label="GitHub">
            </el-table-column>
            <el-table-column
                fixed="right"
                label=""
                width="180">
                <template slot-scope="scope">
                    <el-button type="text"  @click="githubClickA(scope.row)" size="small"></el-button>
                    <el-button type="text" @click="githubClick(scope.row)" size="small">GitHub</el-button>
                    <el-button @click="handleClick(scope.row)"  type="text" size="small"></el-button>
                    <el-button @click="removeClick(scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-row class="foots">
                <el-col :xs="{span: 24}" :sm="{span: 18}" :md="{span: 12, offset: 8}" :lg="{span: 8, offset: 12}" :xl="{span: 6, offset: 16}" justify="end">
                    <div class="grid-content bg-purple-dark">
                            <el-pagination
                                background
                                layout="prev, pager, next"
                                :total="Numbers"
                                :page-size='NumStr'
                                @current-change="handleCurrentChange"
                                :hide-on-single-page="Numbers/NumStr>1 ? false : true ">
                            </el-pagination>
                    </div>
                </el-col>
        </el-row>
    </div>
</template>

<script>
import { log } from 'util';
import moment from 'moment'

import Breadcrumb from '../breadcrumb/Breadcrumb'
export default {
    data() {
      return {
        breadcrumb:['',''],
        tableData: [],
        Numbers:1,  //
        NumStr:10 ,  //
      }
    },
     methods: {
      handleClick(row) {   //
         this.$router.push({path:'/caseall/casetxt/'+row._id})
      },
      handleCurrentChange:function(val){  //
       //  console.log(`: ${val}`);
         this.secrchTxtsLi(val)
      },
      githubClick:function(val){
         
         window.location.href=val.tableData.addressB
      },
      githubClickB:function(val){
         
         window.location.href=val.tableData.addressA
      },
      secrchTxtsLi:function(nums=1){ // 
         
            this.$http({  
                method:'POST',
                url:'searchCase',
                data:{
                    num:nums,  //
                    NumStr:this.NumStr
                }
            }).then(res=>{
                //console.log(res)
                if(res.data.code==0){
                // console.log(res.data)
                    this.tableData=res.data.data
                }
            })
      },
      removeClick(row){  //
      if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
          this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
            this.$http({
                method:'POST',
                url:'removeCase',
                data:{
                    ids:row._id
                }
            }).then(res=>{
                if(res.data.code==0){
                        for(let i=0;i<this.tableData.length;i++){
                            if(this.tableData[i]['_id']==row._id){
                                this.tableData.splice(i,1)
                            }
                        }
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                }else{
                    this.$message.error('');
                }
            })
         
         
                
                
            
        }).catch(() => {
            this.$message({
                type: 'info',
                message: ''
            });          
            });
      }
    },
    created:function(){
        //console.log(this.$store.state.user)
        // 
        // this.$http({  
        //         method:'POST',
        //         url: 'searchtable'
        //     }).then((res)=>{
        //        // console.log(res.data.data)
        //         this.tablesList=res.data.data
        //     })
        // // 
        // this.$http({
        //         method:'POST',
        //         url: 'searchcls'
        //     }).then((res)=>{
        //        // console.log(res.data.data)
        //         this.clsList=res.data.data
        //     })
        
        this.secrchTxtsLi() //

        // 
        this.$http({
            method:'POST',
            url:'searchCaseAllNumber'
        }).then(res=>{
            //console.log(res)
            if(res.data.code==0){
                
                this.Numbers=res.data.data
                //console.log(this.Numbers)
            }
        })

         
    },
    computed:{
    },
    components:{
        Breadcrumb
    }
}
</script>

<style scoped>
.txtbox{
    overflow-y:scroll;
}
.foots{
    margin-top: 30px;
}
</style>
