<template>
    <div class="tables">
         <breadcrumb :breadcrumbs='breadcrumb'></breadcrumb>
        <!--  -->
        <el-form ref="TabForm" :model="formTab" label-width="150px" :rules="formTabs">
            <el-form-item label="" prop="name">
                <el-input v-model="formTab.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="desc">
                <el-input v-model="formTab.desc" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="addressA">
                <el-input v-model="formTab.addressA" placeholder=""  show-word-limit></el-input>
            </el-form-item>
            <el-form-item label="github" prop="addressB">
                <el-input v-model="formTab.addressB" placeholder="github"  show-word-limit></el-input>
            </el-form-item>
            <el-form-item label="" prop="icons">
                    <el-button-group>
                        <el-button v-for="(item,index) in options" :key="index" :icon="'iconfont '+item" @click="onIcon(index)"  :type="index==formTab.indexs ? 'primary' : ''"></el-button>
                    </el-button-group>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit('TabForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

import Breadcrumb from '../breadcrumb/Breadcrumb'
export default {
    data(){
        return {
            breadcrumb:['',''],
            formTab:{
                name:'',
                desc:'',
                addressA:'',
                addressB:'',
                icons:'',
                indexs:-1,
            },
            formTabs:{  //
                name:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 30, message: ' 2  30 ', trigger: 'blur' }
                ],
                desc:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 30, message: ' 3  30 ', trigger: 'blur' }
                ],
                addressA:[
                    { required: true, message: '', trigger: 'blur' }
                ],
                addressB:[
                    { required: true, message: 'github', trigger: 'blur' }
                ],
                icons:[
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
             options: ['icon-socialjavascript','icon-nodejs','icon-vuejs','icon-react','icon-app','icon-houtaishezhi','icon-jquery','icon-shengchengdaima','icon-css','icon-html5']
            }    
    },
    methods:{
        onSubmit:function(formName){
            if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log(this.formTab)
                    this.$http({
                        method:'POST',
                        url: 'updateCase',
                        data:this.formTab
                    }).then((res)=>{
                        if(res.data.code==0){
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'success'
                            });
                            //this.$store.commit('arrClsList',res.data.date)
                            this.formTab.name=''
                            this.formTab.desc=''
                            this.formTab.addressA=''
                            this.formTab.addressB=''
                            this.formTab.icons=''
                            this.$router.go(-1)
                           
                        }else{
                             this.$message({
                                showClose: true,
                                message: '',
                                type: 'warning'
                            });
                        }
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        onIcon:function(index){
            this.formTab.indexs=index
            this.formTab.icons=this.options[index]
        }
        
    },
    components:{
        Breadcrumb
    },
    created:function(){
        let userid=this.$route.params.userid
       this.$http({
           url:'searchCaseOnes',
           method:"POST",
           data:{
               userid:userid
           }
       }).then(res=>{
           if(res.data.code==0){
               this.formTab=res.data.data
           }
       }).catch(()=>{

       })

    }
}
</script>

