<template>
    <div class="tables-box">
        <breadcrumb :breadcrumbs='breadcrumb'></breadcrumb>
        <!--  -->
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            label=""
            type="index"
            width="60">
            </el-table-column>
            <el-table-column
            prop="name"
            label=""
            >
            </el-table-column>
            <el-table-column
            prop="desc"
            label=""
            >
            </el-table-column>
            <el-table-column
            fixed="right"
            label=""
            >
            <template slot-scope="scope">
                <el-button  @click="handleClickC(scope.row)" type="text" size="small"></el-button>
                <el-button  @click="handleClickA(scope.row)" type="text" size="small"></el-button>
                <el-button @click="handleClickB(scope.row)" type="text" size="small"></el-button>
            </template>
            </el-table-column>
        </el-table>
        <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="form" :rules="rules" ref="ruleFormX">
                <el-form-item label="" :label-width="formLabelWidth" prop="name">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="desc">
                    <el-input type="textarea" v-model="form.desc" autocomplete="off" maxlength="30" show-word-limit></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleDialog('ruleFormX')"> </el-button>
            </div>
        </el-dialog>
         <el-row class="foots">
                <el-col :xs="{span: 24}" :sm="{span: 18}" :md="{span: 12, offset: 8}" :lg="{span: 8, offset: 12}" :xl="{span: 6, offset: 16}" justify="end">
                    <div class="grid-content bg-purple-dark">
                            <el-pagination
                                background
                                layout="prev, pager, next"
                                :total="Numbers"
                                :page-size='NumStr'
                                :pager-count='5'
                                @current-change="handleCurrentChange"
                                :hide-on-single-page="Numbers/NumStr>1 ? false : true ">
                            </el-pagination>
                    </div>
                </el-col>
        </el-row>
    </div>
</template>

<script>
import Breadcrumb from '../breadcrumb/Breadcrumb'
export default {
    data() {
      return {
        breadcrumb:['','',''],
        tableData: [],  //
        form: {        //
          name: '',
          desc: ''
         },
         rules: {
            name: [
                { required: true, message: '', trigger: 'blur' },
                { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' }
            ],
            desc: [
                { required: true, message: '', trigger: 'blur' },
                { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
            ]
         },
        dialogTableVisible: false,  
        dialogFormVisible: false,
        formLabelWidth: '120px',   //
        Index:0,   //index
        Numbers:1, //
        NumStr:10   //
      }
    },
    created:function(){
        // 
        this.pageStr()
        // 
       this.numbersArr()
    },
    methods:{
        handleClickC:function(row){
            this.$router.push('/tablebox/tableTextlist/'+row._id)
        },
        handleClickA:function(row){  //
        if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
        //console.log(this.tableData)
             for(let i=0;i<this.tableData.length;i++){
                    if(this.tableData[i]['_id']==row._id){
                             let froms=JSON.stringify(this.tableData[i])
                                this.form=JSON.parse(froms)
                            this.Index=i
                        }
                    }
            this.dialogFormVisible = true
        },
        handleClickB:function(row){  //
        if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }).then(() => {
                    this.$http({
                        url:'searchTableAtxts',
                        method:'POST',
                        data:{
                            id:row._id
                        }
                    }).then(res=>{
                       // console.log(res)
                        if(res.data.code==0){
                            if(res.data.data>0){
                                this.$confirm('', '', {
                                    confirmButtonText: '',
                                    cancelButtonText: '',
                                    type: 'warning'
                                    }).then(() => {
                                        this.$router.push('/tablebox/tableTextlist/'+row._id)
                                    }).catch(() => {
                                    this.$message({
                                            type: 'info',
                                            message: '!'
                                        });         
                                });
                            }else{
                                  this.$http({
                                        url:'deleteTableli',
                                        method:'POST',
                                        data:{
                                            id:row._id
                                        }
                                    }).then(res=>{
                                        if(res.data.code==0){
                                                this.$message({
                                                    type: 'success',
                                                    message: '!'
                                                });
                                                for(let i=0;i<this.tableData.length;i++){
                                                    if(this.tableData[i]['_id']==row._id){
                                                        this.tableData.splice(i,1) 
                                                        }
                                                    }
                                                this.$store.commit('deleteTable',row._id)
                                                this.numbersArr()
                                        }else{
                                            this.$message.error('');
                                        }
                                    }).catch(()=>{
                                        this.$message.error('');
                                    })
                            }
                        }
                    }).catch(()=>{
                         this.$message.error('');
                    })
                          
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        },
        handleDialog:function(formName){  //
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    
                    
                    this.$http({
                        url:'updateTable',
                        method:'POST',
                        data:{
                            forms:this.form
                        }
                    }).then(res=>{
                       // console.log(res)
                        if(res.data.code==0){
                            this.dialogFormVisible = false
                            this.tableData.splice(this.Index,1,this.form)
                             this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.$store.commit('undateTable',this.form)
                        }else{
                            this.dialogFormVisible = false
                            this.$message.error('');
                        }
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });  
        },
        pageStr:function(num=1){ // 
            this.$http({
                method:'POST',
                url: 'searchTablesLists',
                data:{
                    NumStr:this.NumStr,
                    num:num
                }
            }).then((res)=>{
                this.tableData=res.data.data
            })
        },
        handleCurrentChange:function(row){  //
              this.pageStr(row)
        },
        numbersArr:function(){  // 
            
            this.$http({
                    method:'POST',
                    url: 'tableNumber'
                }).then((res)=>{
                    this.Numbers=res.data.data
                })
        }
    },
    components:{
        Breadcrumb
    }
}
</script>

<style scoped>
.foots{
    margin-top: 30px;
}
</style>
