<template>
    <div class="tables">
         <breadcrumb :breadcrumbs='breadcrumb'></breadcrumb>
        <!--  -->
        <el-form ref="TabForm" :model="formTab" label-width="100px" :rules="formTabs">
            <el-form-item label="" prop="users">
                <el-input v-model="formTab.users" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
                <el-input v-model="formTab.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="passwords">
                <el-input v-model="formTab.passwords" placeholder=""  show-word-limit></el-input>
            </el-form-item>
            <el-form-item label="" prop="desc">
                <el-select v-model="formTab.desc" placeholder="">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit('TabForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import axios from '../axios.js'
import Breadcrumb from './breadcrumb/Breadcrumb'
export default {
    data(){
        let validateName=function(rule, value, callback){  //
              axios({
                  url:'searchNames',
                  method:"POST",
                  data:{
                      name:value
                  }
                  }).then(res=>{
                      if(res.data.code==0){
                            if(res.data.data==0){
                                callback();
                            }else{
                                callback(new Error(''));
                            }
                        }else{
                            callback(new Error(''));
                        }
                  }).catch(error=>{
                       callback(new Error(''));
                  })  
                
        };
        let validatereg=function(rule, value, callback){   //
                    let reg=/^[a-zA-Z][a-zA-Z0-9]{3,15}$/;  
                      if(reg.test(value)==true){
                            
                                callback();
                            
                        }else{
                            callback(new Error('(,)'));
                        }
                 
                
        };
         let validatePassReg=function(rule, value, callback){   //
                    let reg=/^[a-zA-Z][a-zA-Z0-9]{6,30}$/;  
                      if(reg.test(value)==true){
                            
                                callback();
                            
                        }else{
                            callback(new Error('()'));
                        }
                 
                
        };
         let validateUserReg=function(rule, value, callback){   //
                    let reg=/[\u4e00-\u9fa5_a-zA-Z0-9_]{2,15}/;  
                      if(reg.test(value)==true){
                                callback();     
                        }else{
                            callback(new Error('()'));
                        }     
        };
        return {
            breadcrumb:['','',''],
            formTab:{
                users:'',
                name:'',
                desc:'',
                passwords:'',
            },
            formTabs:{  //
                users:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' },
                    { validator: validateUserReg, trigger: 'blur' }
                ],
                name:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' },
                    { validator: validatereg, trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                passwords:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 6, max: 30, message: ' 6  30 ', trigger: 'blur' },
                    { validator: validatePassReg, trigger: 'blur' }
                ],
                desc:[
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
             options: [{
                value: '1',
                label: ''
                }, {
                value: '2',
                label: ''
                }]
            }    
    },
    methods:{
        onSubmit:function(formName){
            if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$http({
                        method:'POST',
                        url: 'adduser',
                        data:this.formTab
                    }).then((res)=>{
                        console.log(res)
                        if(res.data.code==0){
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'success'
                            });
                            //this.$store.commit('arrClsList',res.data.date)
                            this.formTab.users=''
                            this.formTab.name=''
                            this.formTab.passwords=''
                            this.formTab.desc=''
                             this.$prompt('', '', {
                                        confirmButtonText: '',
                                        cancelButtonText: '',
                                        inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
                                        inputErrorMessage: ''
                                        }).then(({ value }) => {
                                            this.$message({
                                                type: 'success',
                                                message: ': ' + value
                                            });
                                        }).catch(() => {
                                        this.$message({
                                            type: 'info',
                                            message: ''
                                        });       
                                    });
                        }else{
                             this.$message({
                                showClose: true,
                                message: '',
                                type: 'warning'
                            });
                        }
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
        
    },
    components:{
        Breadcrumb
    }
}
</script>

