<template>
    <div class="tables">
        <breadcrumb :breadcrumbs='breadcrumb'></breadcrumb>
        <el-form ref="TabForm" :model="formTab" label-width="100px" :rules="formTabs">
            <el-form-item label="" prop="name">
                <el-input v-model="formTab.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="desc">
                <el-input type="textarea" v-model="formTab.desc" placeholder=""  maxlength="30" show-word-limit></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit('TabForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import Breadcrumb from './breadcrumb/Breadcrumb'
export default {
    data(){
        return {
            breadcrumb:['','',''],
            formTab:{
                name:'',
                desc:''
            },
            formTabs:{
                name:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' }
                ],
                desc:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
                ]
            }
        }
    },
    methods:{
        onSubmit:function(formName){
            if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$http({
                        method:'POST',
                        url: 'addcls',
                        data:this.formTab
                    }).then((res)=>{
                        console.log(res)
                        if(res.data.code==0){
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'success'
                            });
                            this.$store.commit('arrClsList',res.data.date)
                            this.formTab.name=''
                            this.formTab.desc=''
                        }else{
                             this.$message({
                                showClose: true,
                                message: '',
                                type: 'warning'
                            });
                        }
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    },
    components:{
        Breadcrumb
    }
}
</script>

