<template>
    <div class="box">
        <breadcrumb :breadcrumbs='breadcrumb' class="box-tits"></breadcrumb>
        <!--  -->
        <el-row class="conts">
            <!-- <el-col class="write-btns" :xs="{span: 8, offset: 8}" :sm="{span: 6, offset: 12}" :md="{span: 4, offset: 16}" :lg="{span: 3, offset: 18}" :xl="{span: 2, offset: 20}">
                <div class="grid-content bg-purple-dark">
                    <el-button type="success" @click="submitForm('ruleForm')"></el-button>
                </div>
            </el-col>
            <el-col class="write-btns" :xs="{span: 8, offset: 16}" :sm="{span: 6, offset: 18}" :md="{span: 4, offset: 20}" :lg="{span: 3, offset: 21}" :xl="{span: 2, offset: 22}" justify="end">
                <div class="grid-content bg-purple-dark">
                    <el-button type="success" @click="submitForm('ruleForm')"></el-button>
                </div>
            </el-col> -->
            <el-col class="write-btns" :xs="{span: 8, offset: 8}" :sm="{span: 6, offset: 12}" :md="{span: 4, offset: 16}" :lg="{span: 3, offset: 18}" :xl="{span: 2, offset: 20}" justify="end">
                <div class="grid-content bg-purple-dark">
                    <el-button type="info" @click="submitForm('ruleForm','2')"></el-button>
                </div>
            </el-col>
            <el-col class="write-btns" :xs="{span: 8}" :sm="{span: 6}" :md="{span: 4}" :lg="{span: 3}" :xl="{span: 2}" justify="end">
                <div class="grid-content bg-purple-dark">
                    <el-button type="success" @click="submitForm('ruleForm','1')"></el-button>
                </div>
            </el-col>
        </el-row>
        <el-form ref="ruleForm" :model="form" label-width="100px" :rules="rulesform">
            <el-form-item label=":" prop="name">
                <el-input v-model="form.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label=":" prop="introduction">
                <el-input type="textarea" v-model="form.introduction" placeholder=""  maxlength="150" show-word-limit></el-input>
            </el-form-item>
            <el-form-item label=":" prop="classed">
                <el-select v-model="form.classed" placeholder="">
                    <el-option v-for="(item,index) in clsList" :key="index" :label="item.name" :value="item._id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" prop="checkedA">
            <!-- <div class="write-table"> -->
                    <!-- <span class="write-table-list"></span> -->
                    <!-- <el-checkbox-group v-model="form.checkedA" class="write-table">
                        <el-checkbox  v-for="(item,index) in tablesList" :key="index" :label="item.name" border class="checkli" :name="item.id"></el-checkbox>
                    </el-checkbox-group> -->
                    <el-select
                        v-model="form.checkedA"
                        multiple
                        allow-create
                        class="selectCls"
                        :multiple-limit="multipleLimit"
                        default-first-option
                        placeholder="">
                        <el-option
                        v-for="item in tablesList"
                        :key="item.value"
                        :label="item.name"
                        :value="item._id">
                        </el-option>
                    </el-select>
            <!-- </div> -->
            </el-form-item>
            <el-form-item label=":" prop="tx">
                <div class="write-texts">
                    <write-txts v-model="form.tx" :txts='form.tx'></write-txts>
                </div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import writeTxts from '@/components/draft/Draftwrite'
import Breadcrumb from '../breadcrumb/Breadcrumb'
export default {
    data(){
        return {
            breadcrumb:[''],
            form:{  //
                name:'',  //
                introduction:'',  //
                classed:'',  //
                checkedA:[],  //
                tx:'',   //
                _id:'',
                bst: 1 //12markDown
            },
            rulesform:{ //
                name:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
                ],
                introduction:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 10, max: 150, message: ' 10  150 ', trigger: 'blur' }
                ],
                classed:[
                    { required: true, message: '', trigger: 'change' }
                ],
                checkedA:[
                    { type: 'array', required: true, message: '', trigger: 'change' }
                ],
                tx:[
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            // clsList:[], //
            // tablesList:[], //
            multipleLimit:5
        }
    },
    components:{
        writeTxts,
        Breadcrumb
    },
    methods:{
        // txts:function(tx){
        //     this.form.tx=tx
        // },
        submitForm(formName,tp) {  //
        if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // 
            this.$http({  
                method:'POST',
                url: 'updatewrite',
                data:{
                    forms:this.form,
                    types:tp
                }
            }).then((res)=>{
                //console.log(res.data)
                if(res.data.code==0){
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                        });
                    this.form.name=''
                    this.form.introduction=''
                    this.form.classed=''
                    this.form.checkedA=[]
                    this.$store.commit('addtxB','')
                    this.$router.go(-1)
                }else{
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                }
            })
          } else {
            //console.log(this.form);
            return false;
          }
        });
      }
    },
    created:function(){
        // 
        //this.clsList=this.$store.state.clsList
        // this.$http({  
        //         method:'POST',
        //         url: 'searchcls'
        //     }).then((res)=>{
        //        // console.log(res.data)
        //         this.clsList=res.data.data
        //     })
        // 
        //this.tablesList=this.$store.state.clsList
        // this.$http({
        //         method:'POST',
        //         url: 'searchtable'
        //     }).then((res)=>{
        //         //console.log(res.data)
        //         this.tablesList=res.data.data
        //     })
        // 
        if(!this.$route.query.info){
            this.$router.go(-1)
        }
        this.$http({
                method:'POST',
                url: 'searchtexsli',
                data:{
                    id:this.$route.query.info
                }
            }).then((res)=>{
                //console.log(res.data)
                if(res.data.code=='0'){
                    this.form=res.data.data[0]
                    this.$store.commit('addtxB',res.data.data[0]['tx'])
                    
                }
            })
      
    },
    computed:{
        formtx :function(){
            
            return this.$store.state.txB
        },
        clsList:function(){  //
            return this.$store.state.clsList
        },
        tablesList:function(){  //
            return this.$store.state.tablesList
        }
    },
    watch:{
        formtx:function(news,olds){
            this.form.tx=news
        }
    }
}
</script>

