<template>
    <div class="box-books">
        <el-row>
            <el-col :span="24">
                 <div class="grid-content bg-purple-dark">
                     <el-link type="primary">-()</el-link>
                </div>
                <el-divider></el-divider>
            </el-col>
            <el-col :span="24">
                 <el-form  :model="form" label-width="100px" :rules="ruleForm" ref="ruleForms">
                    <el-form-item label=":" prop="name">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label=":" prop="desc">
                        <el-col :span="11">
                        <el-date-picker type="date" placeholder="" format="yyyy  MM  dd " value-format="yyyy-MM-dd" v-model="form.desc" style="width: 100%;"></el-date-picker>
                        </el-col>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForms')"></el-button>
                    </el-form-item>
                </el-form>
                <el-divider></el-divider>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <el-link type="primary">-()</el-link>
                    </div>
                    <el-divider></el-divider>
                    <el-table
                        :data="tableData"
                        style="width: 100%">
                        <el-table-column
                        type="index"
                        width="80"
                        label="">
                        </el-table-column>
                        <el-table-column
                            prop="desc"
                            label=""
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="name"
                            label="">
                        </el-table-column>
                        <el-table-column
                            fixed="right"
                            label=""
                            width="100">
                            <template slot-scope="scope">
                                <el-button @click="handleClickA(scope.row)" type="text" size="small"></el-button>
                                <el-button @click="handleClickB(scope.row)" type="text" size="small"></el-button>
                            </template>
                            </el-table-column>
                    </el-table>
                </el-col>
            </el-col>
        </el-row>
        <el-dialog title="" :visible.sync="dialogFormVisible">
          
                <el-form  :model="formB" label-width="100px" :rules="ruleForm" ref="ruleFormsB">
                    <el-form-item label=":" prop="name">
                        <el-input v-model="formB.name"></el-input>
                    </el-form-item>
                    <el-form-item label=":" prop="desc">
                        <el-col :span="11">
                        <el-date-picker type="date" placeholder="" format="yyyy  MM  dd " value-format="yyyy-MM-dd" v-model="formB.desc" style="width: 100%;"></el-date-picker>
                        </el-col>
                    </el-form-item>
                </el-form>
           
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="submitFormB('ruleFormsB')"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data(){
        return {
            form:{  //
                name:'', //
                desc:'',  //
                types: '2'  // 1 2
            },
            ruleForm:{   //
                name:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 30, message: ' 3  30 ', trigger: 'blur' }
                ],
                desc:[
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            tableData:[],  // 
            formB:{  //
                name:'', //
                desc:'',  //
                types: '2'  // 1 2
            },
            dialogFormVisible:false  //
        }
    },
    methods:{
        submitForm:function(ruleForms){   //
        if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
            this.$refs[ruleForms].validate((valid) => {
                if (valid) {
                   console.log(this.form)
                    this.$http({
                        url:'addAbout',
                        method:"POST",
                        data:{
                            str:this.form
                        }
                    })
                    .then(res=>{
                        this.tableData.push(res.data.data)
                        this.form.name='',
                        this.form.desc=''
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
        },
        submitFormB:function(ruleForms){   //
        if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
            this.$refs[ruleForms].validate((valid) => {
                if (valid) {
                   console.log(this.form)
                    this.$http({
                        url:'undateAbout',
                        method:"POST",
                        data:{
                            str:this.formB
                        }
                    })
                    .then(res=>{
                        // this.tableData.push(res.data.data)
                        if(res.data.code==0){
                            for(let i=0;i<this.tableData.length;i++){
                                   if(this.tableData[i]['_id']==this.formB._id){
                                        this.tableData[i].name=this.formB.name
                                        this.tableData[i].desc=this.formB.desc
                                    }
                                } 
                             this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                            this.dialogFormVisible=false
                        }
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
        },
        handleClickB:function(row){  //
        if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
                this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                        }).then(() => {
                            this.$http({
                                    url:'allAbout',
                                    method:'POST',
                                    data:{
                                        id:row._id
                                    }
                                }).then(res=>{
                                   for(let i=0;i<this.tableData.length;i++){
                                        if(this.tableData[i]['_id']==row._id){
                                            this.tableData.splice(i,1) 
                                            }
                                        } 
                                   this.$message({
                                        type: 'success',
                                        message: '!'
                                    });
                                }).catch(()=>{
                                    this.$message.error(''); 
                                })
                            
                        }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: ''
                            });          
                    });
        },
        handleClickA:function(row){  //
        if(this.$store.state.user.desc>1){
                 this.$alert(',', '', {
                    confirmButtonText: '',
                        callback: ()=> {
                            this.$message({
                            type: 'info',
                            message: ''
                            });
                        }
                    });
                return false;
            }
                this.dialogFormVisible=true
                this.Indexs=''
                this.formB.name=row.name
                this.formB.desc=row.desc
                this.formB._id=row._id
                this.formB.types=row.types
        }
    },
    created:function(){
        this.$http({
            url:'allAbout',
            method:'POST',
            data:{
                types:this.form.types
            }
        }).then(res=>{
            this.tableData=res.data.data
        }).catch(()=>{

        })
    }
}
</script>

<style scoped>
.box-books{
    height: 100%;
}
</style>
